@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;800&display=swap);
.wrapper{position:relative;margin:0 auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:1440px;max-width:95%}

@font-face{font-family:'Montserrat', sans-serif}.footer{background-color:#222;height:61px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.footer__writter{display:block;color:#ffffff;font-family:'Montserrat', sans-serif;font-weight:600}

.logo-planthor{display:block;float:left}.logo-planthor__link{display:block;float:left}.logo-planthor__link img{display:block;width:140px}

.list-menu{position:absolute;box-sizing:border-box;left:220px}@media (min-width: 375px){.list-menu{display:none}}@media (min-width: 768px){.list-menu{display:block}}.list-menu ul{display:-webkit-flex;display:flex;text-decoration:none}.list-menu ul li .nav-link{color:#000;font-weight:600;padding:1em 1em}

.profile-btn{position:absolute;display:-webkit-flex;display:flex;right:0}@media (min-width: 375px){.profile-btn{display:none}}@media (min-width: 768px){.profile-btn{display:-webkit-flex;display:flex}}.profile-btn .btn-sign-in{background-color:#ffffff;border:2px solid #1e82c5;cursor:pointer;padding:.3em 1em;margin:0 8px}.profile-btn .btn-sign-in:hover{color:#ffffff;transition:0.5s;background:#1e82c5}.profile-btn .btn-sign-up{background-color:#1e82c5;cursor:pointer;border:2px solid #1e82c5;color:#ffffff;padding:.3em 1em}.profile-btn .btn-sign-up:hover{color:#222;border:2px solid #1e82c5;transition:0.5s;background:#ffffff}

.btn{font-family:"Montserrat", sans-serif;font-weight:600;border-radius:.3rem;cursor:pointer}

.bg-navigationbar{position:fixed;display:-webkit-flex;display:flex;z-index:9999;-webkit-align-items:center;align-items:center;background-color:#ffffff;height:50px;width:100%;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}

.home-page{background-image:url(/static/media/background.b23fb868.png);background-repeat:no-repeat;background-size:cover;background-position:bottom;position:relative;height:839px}@media (min-width: 1601px){.home-page{height:1110px}}.home-page__content{position:absolute;top:245px;width:557px}.home-page__content h2{font-weight:800;text-align:justify;color:#222;text-align:left;font-size:84px}.home-page__content p{display:-webkit-flex;display:flex;text-align:justify;text-align:left;font-weight:500;font-size:25px;color:#222}

@font-face{font-family:'Montserrat', sans-serif}body{font-family:'Montserrat', sans-serif;padding:0;margin:0;box-sizing:border-box}body a{font-family:'Montserrat', sans-serif;text-decoration:none}body a:hover{text-decoration:none}body img{width:100%}body h1,body h2,body h3,body h4,body h5,body h6{font-family:'Montserrat', sans-serif;margin:0}body ul,body ol{margin:0;padding:0}body ul li,body ol li{list-style-type:none}

