.profile-btn {
    position: absolute;
    display: flex;
    right: 0;

    @media (min-width:375px) {
        display: none;
    }

    @media (min-width:768px) {
        display: flex;
    }

    .btn-sign-in {
        background-color: #ffffff;
        border: 2px solid #1e82c5;
        cursor: pointer;

        padding: .3em 1em;
        margin: 0 8px;


        &:hover {
            color: #ffffff;
            transition: 0.5s;
            background: #1e82c5;
        }
    }

    .btn-sign-up {
        background-color: #1e82c5;
        cursor: pointer;
        border: 2px solid #1e82c5;
        color: #ffffff;

        padding: .3em 1em;

        &:hover {
            color: #222;
            border: 2px solid #1e82c5;
            transition: 0.5s;
            background: #ffffff;
        }
    }
}